@import "components/header";
@import "components/aside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-9{
          .products{
            &__compare{
              display: flex;
              justify-content: space-between;
              .mobileSort{
                display: none;
                flex-direction: column;
                position: relative;
                span{
                  padding: 0 25px 0 0;
                  display: block;
                  font: .875em/3em "Arial";
                  color: #0081ef;
                  &:hover{
                    cursor: pointer;
                  }
                }
                .sortOptions{
                  position: absolute;
                  top: 35px;
                  background-color: #fff;
                  white-space: nowrap;
                  z-index: 5;
                  .filter{
                    margin: 5px 0;
                    display: flex;
                    align-items: center;
                    p{
                      font: 1em/1.125em "Arial";
                      font-weight: bold;
                      color: #777;
                    }
                    select{
                      margin: 0 15px;
                      padding: 0 10px;
                      height: 25px;
                      font: .938em/25px "Arial";
                      color: #4c4c4c;
                      background-color: #eaeaea;
                      outline: none;
                    }
                  }
                  .sortBy{
                    display: flex;
                    flex-direction: column;
                    .sort,
                    .quantity{
                      margin: 5px 0;
                      display: flex;
                      align-items: center;
                      p{
                        font: 1em/1.125em "Arial";
                        font-weight: bold;
                        color: #777;
                      }
                      select{
                        margin: 0 15px;
                        padding: 0 10px;
                        height: 25px;
                        font: .938em/25px "Arial";
                        color: #4c4c4c;
                        background-color: #eaeaea;
                        outline: none;
                      }
                    }
                  }
                }
              }
              a{
                padding: 0 25px 0 0;
                display: block;
                font: .875em/3em "Arial";
                color: #0081ef;
                text-decoration: none;
              }
            }
            &__sortOptions{
              display: flex;
              justify-content: space-between;
              .filter{
                display: flex;
                align-items: center;
                p{
                  font: 1em/1.125em "Arial";
                  font-weight: bold;
                  color: #777;
                }
                select{
                  margin: 0 15px;
                  padding: 0 10px;
                  height: 25px;
                  font: .938em/25px "Arial";
                  color: #4c4c4c;
                  background-color: #eaeaea;
                  outline: none;
                }
              }
              .sortBy{
                display: flex;
                .sort,
                .quantity{
                  display: flex;
                  align-items: center;
                  p{
                    font: 1em/1.125em "Arial";
                    font-weight: bold;
                    color: #777;
                  }
                  select{
                    margin: 0 15px;
                    padding: 0 10px;
                    height: 25px;
                    font: .938em/25px "Arial";
                    color: #4c4c4c;
                    background-color: #eaeaea;
                    outline: none;
                  }
                }
              }
            }
            &__cart{
              margin: 15px 0;
              width: 100%;
              display: flex;
              flex-wrap: wrap;
              justify-content: space-between;
              .cart{
                margin: 15px 0;
                width: 30%;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                position: relative;
                border: 1px solid #dfdfdf;
                a{
                  display: block;
                  text-decoration: none;
                  img{
                    width: 100%;
                    padding: 15px 20px;
                  }
                  span{
                    display: block;
                    padding: 15px 20px;
                    font: .875em/1em "Arial";
                    font-weight: bold;
                    color: #0081ef;
                  }
                }
                ul{
                  display: flex;
                  li{
                    a{
                      display: block;
                      font: .875em/40px "Arial";
                      font-weight: bold;
                      color: #777;
                      text-align: center;
                      background-color: #eee;
                      border-top: 1px solid #dfdfdf;
                    }
                  }
                  .inCart{
                    width: 61%;
                  }
                  .heart,
                  .exchange{
                    width: 50px;
                    border-left: 1px solid #dfdfdf;
                  }
                }
                .hover{
                  display: none;
                  width: 254px;
                  position: absolute;
                  bottom: -150px;
                  left: -1px;
                  background-color: #fff;
                  border: 1px solid #78afeb;
                  border-top: none;
                  box-shadow: 5px 5px 15px #cecece;
                  z-index: 5;
                  .descr{
                    padding: 10px 20px;
                    display: flex;
                    flex-direction: column;
                    li{
                      p{
                        font: .875em/1.375em "Arial";
                        font-weight: bold;
                        span{
                          padding: 0 0 0 5px;
                          font-weight: normal;
                        }
                      }
                    }
                  }
                  .about{
                    padding: 0 20px 15px;
                    font: .875em/1em "Arial";
                    color: #636363;
                  }
                }
                &:hover{
                  border-color: #78afeb;
                  border-bottom-color: #dfdfdf;
                  box-shadow: 5px 5px 15px #cecece;
                  .hover{
                    display: block;
                  }
                }
              }
            }
            &__pagination{
              margin: 150px 0 0;
              display: flex;
              justify-content: center;
              align-items: flex-end;
              ul{
                display: flex;
                justify-content: center;
                li{
                  margin: 0 5px;
                  width: 45px;
                  height: 40px;
                  background-color: #efefef;
                  border: 1px solid #dedede;
                  span{
                    display: block;
                    font: 1.438em/40px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-align: center;
                    i{
                      font-weight: bold;
                    }
                    &:hover{
                      cursor: pointer;
                    }
                  }
                }
                .active{
                  background-color: #fff;
                  border-color: #0081ef;
                }
              }
              .showMore{
                width: 350px;
                display: none;
                font: 1.188em/40px "Arial";
                font-weight: bold;
                color: #0081ef;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: #efefef;
                border: 1px solid #dedede;
                &:hover{
                  background-color: #fff;
                }
              }
            }
          }
        }
        .col-sm-12{
          .aside{
            display: none;
          }
        }
      }
    }
  }
}