body{
  .content{
    .container{
      .row{
        .col-lg-3,
        .col-sm-12{
          .aside{
            margin: 0 0 30px;
            &__vhLogo {
              height: 105px;
              display: flex;
              flex-direction: column;
              justify-content: center;
              background-image: url("../img/vh_logo.jpg");
              background-size: 100% 100%;
              p {
                padding: 0 0 0 130px;
                font: 2.438em/.875em "BravoSC";
                color: #d10000;
                &:nth-child(1) {
                  font: 1.625em/.875em "BravoSC";
                }
              }
            }
            &__menu {
              margin: 30px 0;
              ul {
                li {
                  a {
                    padding: 0 0 0 20px;
                    display: block;
                    font: .938em/30px "Arial";
                    font-weight: bold;
                    color: #0081ef;
                    text-decoration: none;
                    border: 1px solid #c4c4c4;
                    &:hover {
                      background-color: #d1efff;
                    }
                  }
                }
              }
            }
            &__bestHolo,
            &__newHolo,
            &__threeD {
              margin: 30px 0;
              border: 1px solid #c4c4c4;
              a {
                display: block;
                text-decoration: none;
                h2 {
                  padding: 0 0 0 15px;
                  font: .938em/40px "Arial";
                  font-weight: bold;
                  color: #777;
                  text-transform: uppercase;
                  background-color: #eee;
                  border-bottom: 1px solid #c4c4c4;
                }
                span {
                  display: block;
                  padding: 20px 15px;
                  font: .875em/1.25em "Arial";
                  font-weight: bold;
                  color: #0081ef;
                  text-transform: uppercase;
                }
                img {
                  width: 100%;
                  padding: 15px;
                }
              }
            }
            &__examples {
              margin: 30px 0;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              border: 1px solid #c4c4c4;
              h2 {
                padding: 0 0 0 15px;
                font: .938em/40px "Arial";
                font-weight: bold;
                color: #777;
                text-transform: uppercase;
                background-color: #eee;
                border-bottom: 1px solid #c4c4c4;
              }
              ul {
                li {
                  display: flex;
                  flex-wrap: wrap;
                  a {
                    width: 50%;
                    display: inline-block;
                    img {
                      width: 100%;
                      padding: 10px;
                    }
                  }
                }
              }
              .controls {
                display: flex;
                .prev,
                .next {
                  width: 50%;
                  display: flex;
                  justify-content: space-around;
                  align-items: center;
                  font: .875em/40px "Arial";
                  color: #0081ef;
                  text-align: center;
                  background-color: #eee;
                  border-top: 1px solid #c4c4c4;
                  .dot {
                    font-size: .4em;
                    color: #777;
                  }
                  &:hover {
                    cursor: pointer;
                  }
                }
              }
            }
            &__custom {
              margin: 30px 0;
              border: 1px solid #c4c4c4;
              h2 {
                padding: 10px 0 10px 15px;
                font: .938em/1.25em "Arial";
                font-weight: bold !important;
                color: #0081ef;
                text-transform: uppercase;
                background-color: #eee;
                border-bottom: 1px solid #c4c4c4;
              }
              p {
                padding: 10px 15px;
                font: .875em/1em "Arial";
                color: #777;
                a {
                  color: #0081ef;
                }
              }
            }
          }
        }
      }
    }
  }
}